<template>
    <div class="wrap">
        <div class="bus-out">
            <div class="bus-info">

                <div class="title-left">
                    <span class="score">{{seller.score}}</span>
                    <span class="score-all">综合评分</span>
                    <span>高于周边商家{{seller.rankRate}}%</span>
                </div>
                <div class="title-right">
                    <div><span>商家态度</span>
                        <img class="star-img" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star.png" alt="" v-for="i in serviceScore">
                    <img class="star-img1" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star-2.png" alt="" v-for="i in (5-serviceScore)">
                    </div>
                <div><span>商家评分</span>
                    <img class="star-img" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star.png" alt="" v-for="i in foodScore">
                    <img class="star-img1" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star-2.png" alt="" v-for="i in (5-foodScore)"></div>
                <div><span>送达时间</span><span class="comment-time">{{seller.deliveryTime}}分钟</span></div>
            </div>
        </div>
    </div>

    <div class="bus-out">
        <div class="bus-info info-attitude">
            <span class="attitude-span1">全部{{seller.ratingCount}}</span>
            <span class="attitude-span2">满意18</span>
            <span class="attitude-span3">不满意6</span>
        </div>

        <div class="bus-info">
            <img v-show="!choose" @click="changeState()" class="info-img" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/gouxuan.png" alt="">
            <img v-show="choose" @click="changeState" class="info-img" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/gouxuaned.png" alt="">
            <span class="info-span">只看有评价的内容</span>
        </div>


        <div class="bus-info" v-for="item in commentData">
            <img class="default-head" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/default-head.png" alt="">
            <div class="comment-info">
                <div class="phone"><span v-show="">{{item.username}}</span></div>
                <div class="star">
                    <img class="star-img2" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star.png" alt="" v-for="i in item.score">
                    <img class="star-img3" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/star-2.png" alt="" v-for="i in (5-item.score)"></div>
                <div class="comment-use"><span>{{item.text}}</span></div>
                <div class="comment-foot">
                    <img @click="changeState" v-show="!dianzan" class="dianzan" src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/dianzan.png" alt="">
                    <!--<img src="http://lgting.oss-cn-beijing.aliyuncs.com/icon/dianzan-2.png" alt="">-->
                    <span v-for="food in item.recommend">{{food}}</span>

                </div>
            </div>
        </div>
    </div>

    </div>

</template>

<script>
    import '../public/style.css'

    export default {
        name: "Comment",
        data(){
            return{
                choose:false,
                // dianzan
            }

        },
        computed: {
            seller() {
                return this.$store.state.productList.seller
            },
            //评论
            comment() {
                return this.$store.getters.commontTotal
            },
            commentData(){
                if(this.choose){//全部
                    return this.comment.filter(item=>item.text)

                }else{//只看有评价
                    return this.commentData=this.comment
                }},
        //    服务态度
            serviceScore(){
                return parseInt(this.$store.state.productList.seller.serviceScore)
            },
        //    商品评分
            foodScore(){
                return parseInt(this.$store.state.productList.seller.foodScore)
            },
        },
        methods:{
            changeState(){
                this.choose = !this.choose
                // if(this.choose){//全部
                //     this.commentData=this.comment.filter(item=>item.text)
                //
                // }else{//只看有评价
                //     // this.commentData=this.comment
                // }
            }
        }
    }
</script>

<style scoped>
    .wrap {

        width: 100%;
        overflow: scroll;
        background: #EAEBED;
        height: 100%;
        position: fixed;
        bottom: -15px;
        top: 427px;
    }

    .wrap::-webkit-scrollbar {
        display: none;
    }

    .title-left {
        display: inline-block;
        width: 145px;
        border-right: 1px solid #DBDCDE;
    }

    .title-left span {
        width: 100%;
        text-align: center;
        position: relative;
        vertical-align: top;
        /*top: -14px;*/
    }

    .title-left .score {
        padding-top: 10px;
        width: 100%;
        text-align: center;
        color: orange;
        font-size: 27px;
    }

    .title-left .score-all {
        color: black;
        vertical-align: middle;
    }

    .title-right {
        display: inline-block;
        width: 70%;
        position: relative;
        top: -12px;
        margin-left: 14px;
    }

    .title-right span {
        margin-right: 10px;
        color: black;

    }

    .title-right .comment-time {
        color: gray;
    }

    .info-attitude span {
        padding: 5px 10px;
    }

    .info-attitude .attitude-span1 {
        margin-left: 5px;
        background: #00A4DC;
        color: #fff;
    }

    .info-attitude .attitude-span2 {
        background: #CCECF9;
        color: black;

    }

    .info-attitude .attitude-span3 {
        background: #DBDCDE;
        color: black;
    }

    .info-span {
        vertical-align: middle;
    }

    .info-img {
        vertical-align: middle;

        margin-left: 5px;

    }

    .comment-info {
        display: inline-block;
    }

    .default-head {
        margin-left: 5px;
        vertical-align: top;
        margin-top: 5px;
        margin-right: 5px;
    }

    .comment-use span {
        color: black;
        vertical-align: top;
        margin-top: -9px;
    }

    .phone span {
        vertical-align: top;
        margin-top: 3px;
    }

    .star span {
        vertical-align: top;
        margin-top: -5px;
    }

    .dianzan {
        width: 15px;
        height: 15px;
    }

    .comment-foot {
        position: relative;
        margin-top: -17px;
    }

    .comment-foot img {
        margin-right: 5px;
        /*vertical-align: middle;*/
    }

    .comment-foot span {
        padding: 0px 3px;
        border: 1px solid;
        margin-left: 5px;
    }


    .star-img{
        vertical-align: middle;
        margin-right: 4px;
        width: 20px;
        height: 20px;
    }
    .star-img1{
        vertical-align: middle;
        width: 20px;
        height: 20px;
        margin-left: -9px;
        margin-right: 10px;
        margin-top: 1px;
    }
    .star-img2{
        width: 20px;
        height: 20px;
        vertical-align: top;
        margin-right: 4px;
        margin-top: -9px;
        margin-left: -3px;
    }
    .star-img3{
        width: 20px;
        height: 20px;
        vertical-align: top;
        margin-top: -9px;
        margin-left: -9px;
        margin-right: 10px;
    }
</style>


